/**
The variables wrapper

# Default theme color from WordPress

:root {
  --wp-admin-theme-color: #007cba;
  --wp-admin-theme-color--rgb: 0,124,186;
  --wp-admin-theme-color-darker-10: #006ba1;
  --wp-admin-theme-color-darker-10--rgb: 0,107,161;
  --wp-admin-theme-color-darker-20: #005a87;
  --wp-admin-theme-color-darker-20--rgb: 0,90,135;
  --wp-admin-border-width-focus: 2px;
}
 */
.dm__dashboard-page {
  --dm--box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 0px 1px;
  --dm--box-padding: 16px 24px;
  --dm--text-in-theme-color: #fff;
  --dm--text-default-color: #3c434a;
  --dm--input-distance-bottom: 4px;
}

.dm__box {
  background: #fff;
  box-shadow: var(--dm--box-shadow);
  border-radius: 1px;
  overflow: hidden;
  padding: var(--dm--box-padding);
  max-width: 504px;
}

.admin_page_dm-migration-new-api #wpwrap {
  top: 0;
}
.admin_page_dm-migration-new-api #adminmenumain, .admin_page_dm-migration-new-api #wpfooter, .admin_page_dm-migration-new-api #wpadminbar {
  display: none;
}
.admin_page_dm-migration-new-api #wpcontent {
  margin-left: 0 !important;
  padding-left: 0 !important;
}
.admin_page_dm-migration-new-api .wrap {
  margin: 0 !important;
}

.dm__step-guidance {
  background: var(--wp-admin-theme-color);
  color: var(--dm--text-in-theme-color);
  margin-left: -22px;
  margin-top: -10px;
  width: 100%;
  padding: 14px 22px;
}
.dm__step-guidance a {
  color: inherit;
}

.dm__input {
  width: 100%;
  margin-bottom: var(--dm--input-distance-bottom);
}

.checkbox-label {
  margin-right: 10px;
  margin-bottom: var(--dm--input-distance-bottom);
  display: inline-block;
}

.wp-core-ui .button-danger, .wp-core-ui .button-danger:focus {
  background: #d63638;
  color: #fff;
  border-color: #d63638;
}
.wp-core-ui .button-danger:focus {
  box-shadow: 0 0 0 1px #fff, 0 0 0 3px #2271b1;
}
.wp-core-ui .button-danger:hover {
  background: #b42527;
  color: #fff;
  border-color: #b42527;
}

.box--center {
  margin-right: auto;
  margin-left: auto;
}

.dm--margin-top-3 {
  margin-top: 36px;
}

.dm--margin-bottom-2 {
  margin-bottom: 20px;
}

.dm--margin-left-1 {
  margin-left: 10px;
}

.dm--outstanding-link {
  font-size: 1.6em;
  font-weight: 700;
  line-height: 1.2;
  display: inline-block;
  background: -webkit-linear-gradient(#fee140, #fa709a);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}